
import React, {useRef, useState} from 'react';
import Input from '../../../UI/input';
import CustomButton from '../../Layout/Button/custom-button';

import styles from './MealItenForm.module.css';

function MealItenForm(props) {
    const [amountIsValid, setAmountIsValid] = useState(true);
    const amountInputRef = useRef();

    const submitHandler = (event) => {
        event.preventDefault();
        const enteredAmount = amountInputRef.current.value;
        const enteredAmountNumber = +enteredAmount;
        if(enteredAmount.trim().length === 0 || enteredAmountNumber < 1 || enteredAmountNumber > 5){
            setAmountIsValid(false);
            return;
        }
        props.onAddToCart(enteredAmountNumber);
    }
    return (
        <form onSubmit={submitHandler}>
            <Input ref = {amountInputRef} label='Amount' input={{
                id: 'amount_' + props.id,
                type: 'number',
                min: '1',
                max: '15',
                step: '1',
                defaultValue: '1'
            }} />
            <CustomButton isCart={true}>+ Add</CustomButton>
            {!amountIsValid && <p>Please enter a valid amount (0-5)</p>}
        </form>
    )
}

export default MealItenForm;
